'use client';

import { useState } from 'react';
import Link from 'next/link';
import BottomNavigation from '../components/BottomNavigation';

export default function OrdersPage() {
    const [orders] = useState([
        {
            id: '1',
            route: 'Warsaw → Paris',
            date: 'Apr 16, 2024',
            price: 70.00,
            status: 'Delivered'
        },
        {
            id: '2',
            route: 'Within Kraków',
            date: 'Apr 4, 2024',
            price: 8.00,
            status: 'Delivered'
        },
        {
            id: '3',
            route: 'Poznań → Berlin',
            date: 'Mar 22, 2024',
            price: 50.00,
            status: 'Delivered'
        }
    ]);

    return (
        <div className="min-h-screen bg-black text-white flex flex-col">
            {/* Status Bar */}
            <div className="flex justify-between items-center p-4 pt-12 text-sm">
                <span>9:41</span>
                <div className="flex items-center gap-1">
                    <div className="flex gap-1">
                        <div className="w-1 h-1 bg-white rounded-full"></div>
                        <div className="w-1 h-1 bg-white rounded-full"></div>
                        <div className="w-1 h-1 bg-white rounded-full"></div>
                        <div className="w-1 h-1 bg-white rounded-full"></div>
                    </div>
                    <svg className="w-4 h-4" viewBox="0 0 24 24" fill="white">
                        <path d="M2 17h20v2H2v-2zm1.15-6.05L4 9.47l.85 1.48H5c.55 0 1 .45 1 1s-.45 1-1 1-.45-.45-.45-1zm5.5 2.5H9c.55 0 1-.45 1-1s-.45-1-1-1-.45.45-.45 1z" />
                    </svg>
                    <div className="w-6 h-3 border border-white rounded-sm">
                        <div className="w-4 h-1.5 bg-white rounded-sm m-0.5"></div>
                    </div>
                    <svg className="w-4 h-4" viewBox="0 0 24 24" fill="white">
                        <path d="M12 2C13.1 2 14 2.9 14 4C14 5.1 13.1 6 12 6C10.9 6 10 5.1 10 4C10 2.9 10.9 2 12 2Z" />
                    </svg>
                </div>
            </div>

            {/* Header */}
            <div className="px-8 mb-8">
                <h1 className="text-3xl font-bold">Order History</h1>
            </div>

            {/* Orders List */}
            <div className="flex-1 px-8 space-y-4">
                {orders.map((order) => (
                    <div key={order.id} className="bg-gray-900 rounded-lg p-4">
                        <div className="flex items-center justify-between">
                            <div>
                                <h3 className="text-white text-lg font-semibold mb-1">{order.route}</h3>
                                <p className="text-gray-400 text-sm">{order.date}</p>
                            </div>
                            <div className="text-right">
                                <div className="text-white text-lg font-bold">€{order.price.toFixed(2)}</div>
                                <div className="inline-flex items-center px-2 py-1 rounded-full text-xs bg-green-900 text-green-300 mt-1">
                                    {order.status}
                                </div>
                            </div>
                        </div>
                    </div>
                ))}
            </div>

            {/* Pickom Footer */}
            <div className="text-center py-8">
                <h2 className="text-4xl font-bold">Pickom</h2>
            </div>

            {/* Bottom Navigation */}
            <BottomNavigation />
        </div>
    );
} 